import React, { lazy } from 'react'
import {
  CBadge,
  CButton,
  CButtonGroup,
  CCard,
  CCardBody,
  CCardFooter,
  CCardHeader,
  CCol,
  CProgress,
  CRow,
  CCallout
} from '@coreui/react'
import {requestOptions,Mytime,Mydate,myAssignment} from '../Config'


class My_assignment extends React.Component {
    constructor(props) {
      super(props);
      this.state = {
        data: []
      };
    }
    componentDidMount() {
          fetch(myAssignment, requestOptions)
           .then((response) => response.json())
           .then((data) => {  this.setState({data:data})  })
           .catch(error => console.log("Error detected: " + error))
       }
    render() {

        
        return  <> 
      <CRow>
        <CCol>
          <CCard>
           
            <CCardBody>
              

              <br />

              <table className="table table-hover  table-responsive-md">
                <thead className="thead-light">
                  <tr>
             
                    <th>S.no</th>
                    <th>Batch name</th>
                    <th>Title</th>
                    <th>submission Date and time</th>               
                    <th>Scheduled Date and time</th>               
                    <th className="">Action</th>
                 
                  </tr>
                </thead>
                <tbody>

            {this.state.data.map((list,index) => (
              <tr key={index}>
              
                <td  className="border-left" width="5%"><div>{index+1}</div></td>
                <td  width="15%" > {list.batch_name} </td>
                <td  width="15%" > {list.assignment_name} </td>
                <td> <div className="small text-muted" >{Mydate(list.submission_time)}, {Mytime(list.submission_time)}</div></td>
                <td> <div className="small text-muted" >{Mydate(list.scheduled_time)}, {Mytime(list.scheduled_time)}</div> </td>
                <td className=" border-right">
                    <CRow className=" row">
                        <CCol   className="mb-3 col-12 col-lg-12">
                          <CButton block color="primary">View</CButton>
                        </CCol>             
                     </CRow>
                </td>
              </tr>
   ))}
                  
                </tbody>
              </table>

            </CCardBody>
          </CCard>
        </CCol>
      </CRow>
      </>;
        }

}

export default My_assignment
